import React from 'react';
import {HeadingXLarge, HeadingLarge, LabelLarge, HeadingSmall, LabelMedium, HeadingXSmall} from 'baseui/typography';
import presentation from '../assets/presentation.png';

import {Avatar} from "baseui/avatar";
import BasketBall from '../assets/basketball.png';
import ICSME from '../assets/ICSME.png';
import slide1 from '../assets/slide1.png';
import slide2 from '../assets/slide2.png';
import slide3 from '../assets/slide3.png';
import slide4 from '../assets/slide4.png';
import slide5 from '../assets/slide5.png';
import slide6 from '../assets/slide6.png';
import { IoLogoGithub } from "react-icons/io";
import { StyledLink } from "baseui/link";
import { ListItem, ListItemLabel } from "baseui/list";
import {Card} from "baseui/card";
import { Check, ArrowRight } from "baseui/icon";
import {StarRating} from "baseui/rating";
import { StyledTable, StyledHeadCell, StyledBodyCell } from "baseui/table-grid";
import {Pagination} from "baseui/pagination";
import software from '../assets/software.png';

export default function Study(){
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
      };
    const slideNum = 6
    const images = [slide1, slide2, slide3, slide4, slide5, slide6]
    const [currentPage, setCurrentPage] = React.useState(3);
    return (
        <>
            <LabelLarge>
                Study
            </LabelLarge>
            <Column title={""}>
                
                
                
                 RENAS-p
                
                RENAS-p 
                 F  0.1 
                 RENAS
            </Column>
            <Column title="">
            <img src={images[currentPage-1]}/>
            <Pagination
                numPages={slideNum}
                currentPage={currentPage}
                onPageChange={({ nextPage }) => {
                    var nextPage = Math.min(Math.max(nextPage, 1), 20);
                    setCurrentPage(nextPage);
                }}
                />
            </Column>
            <Column title={""}>
                    <div className="pl-6">
                        <div className="flex">
                            <a href="https://www.sigse.jp/" className="basis-1/5 pt-2"><img src={software}></img></a>
                        </div>
                        <LabelLarge className="my-1">20243</LabelLarge>
                        <div className="flex">
                            <a href="https://conf.researchr.org/home/icsme-2024" className="basis-1/5 pt-4"><img src={ICSME}></img></a>
                        </div>
                        <LabelLarge className="my-1">202410 The International Conference on Software Maintenance and Evolution (Research Track)</LabelLarge>
                        <LabelLarge className="my-2">202410The International Conference on Software Maintenance and Evolution (Artifact Evaluation Track and ROSE Festival Track)</LabelLarge>
                    </div>
                    <HeadingXSmall className="pt-3"></HeadingXSmall>
                    <div className="pl-6">
                        <StyledLink href="https://www.ipsj.or.jp/award/se-award1.html"><LabelLarge className="text-white"></LabelLarge></StyledLink>
                        <StyledLink href="https://www.ipsj.or.jp/award/yamasita2024-detail.html" ><LabelLarge className="text-white"></LabelLarge></StyledLink>
                </div>
            </Column>
            <figure>
                <img src={presentation} width={600}></img>
                <figcaption><LabelLarge></LabelLarge></figcaption>
            </figure>
        </>
    );
}

function Column({title, children}){
    return(
        <div>
            <HeadingSmall className="pt-4">{title}</HeadingSmall>
            <div className="pl-4 pt-2"><LabelMedium><div className="leading-6">{children}</div></LabelMedium></div>
            <hr className="border-gray-500 my-8 w-full" />
        </div>
    );
}
